@import url('https://fonts.googleapis.com/css2?family=Cairo&display=swap');
body {
  margin: 0px;
  direction: rtl;
  font-family: 'Cairo', sans-serif;
}

.navbar ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background: #fff2e6;
}

.navbar li {
  float: right;
  margin-bottom: 0;
}

.navbar li:last-child {
  float: left !important;
}

.navbar li a {
  display: block;
  color: #f25555;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  border-bottom: 2px solid #fff2e6
}

.navbar li a:hover {
  border-bottom: 2px solid #f25555;
}

.header-icon {
  margin-left: 5px;
}

.container {
  padding: 16px;
}

h4 {
  color: #f25555;
  font-size: 20px;
}

hr {
  border: 1px solid #f1f1f1;
  margin-bottom: 25px;
}

blockquote {
  padding: 0 10px;
  color: #f5281af5;
  font-weight: bold;
  border-right: 0.3rem solid #f5291af5;
  border-left: none;
}

input[type=text], input[type=password], input[type=email], textarea {
  width: 95%;
  padding: 15px;
  margin: 10px 0;
  display: block;
  border: 1px solid #967c7c;
  border-radius: 8px;
}

textarea {
  height: 80px;
}

input[type=text]:focus, input[type=password]:focus, input[type=email]:focus, textarea:focus {
  background-color: #f1f1f1;
  outline: none;
}

input[type=submit], .delete, .edit {
  background-color: #04AA6D;
  font-family: 'Cairo', sans-serif;
  color: white;
  font-size: 14px;
  padding: 10px 15px;
  margin: 20px 10px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  opacity: 0.85;
}

.delete-icon, .edit-icon {
  color: white;
  font-size: 14px;
  padding: 10px 15px;
  margin: 0px 7px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  opacity: 0.85;
}

.delete, .delete-icon {
  background-color: #d11 !important;
}

.edit, .edit-icon {
  background-color: #24a0ed !important;
}

.delete-edit {
  position: absolute;
  top: 58%;
  right: 0;
}

input[type=submit]:hover, .delete:hover, .edit:hover, .delete-icon:hover, .edit-icon:hover {
  opacity: 1;
}

.d-none {
  display: none;
}

.photo {
  max-width: 100%;
  max-height: 400px;
  display: block;
  margin: 10px auto;
  cursor: pointer;
}

.posts {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}

.card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  width: 270px;
  height: 330px;
  position: relative;
}

.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

.card h4, .card p {
  margin: 0;
}

.details {
  padding: 2px 16px;
}

.card img {
  width: 100%;
  height: 230px;
}

.cut-text {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.show-photo {
  margin: 0 auto;
  display: block;
 max-width: 100%;
  max-height: 500px;
}

.unlike {
  color: white;
  background: rgba(0, 0, 0, 0.5);
  position: absolute;
  top: 58%;
  left: 3%;
  font-size: 16px;
  padding: 7px 10px;
  margin: 0px 7px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
}

.unlike:hover {
  background-color: rgba(0, 0, 0, 0.7);
}

.like {
  color: red !important;
}

.like-num {
  color: white !important;
}

.heart {
  padding-right: 5px;
}

.comment-author {
  display: block;
}

.comment {
  border-right: 1px solid black;
  padding: 5px 15px;
  background-color: #eee;
}

.comment-num {
  color: #000;
  margin-right: 5px;
}

@media(max-width: 767px) {
  .card {
    max-width: 100%;
    margin: 0 auto;
  }
  .posts {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  .header-item{
    display: none;
  }
}